* {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

input[type=text] {
  background-color: transparent;
  border: 0;
  border-bottom: solid 1px #f16051;
  width: 30%;
  padding-bottom: 4px;
  color: #fff !important;
  font-weight: lighter;
  letter-spacing: 2px;
  margin-bottom: 30px;
  margin-right: 20px;
  font-size: 20px;
  outline: none; }
  input[type=text]:-webkit-autofill {
    -webkit-box-shadow: 0 0 0 30px #e6804e inset;
    -webkit-text-fill-color: #fff !important; }

button {
  border: 0;
  padding: 8px 20px;
  margin: 0 2px;
  border-radius: 2px;
  font-weight: lighter;
  letter-spacing: 1px;
  font-size: 15px;
  cursor: pointer;
  background-color: #49aee9;
  color: #fff;
  font-weight: 100; }
  button:hover {
    background: -webkit-gradient(linear, left top, left bottom, color-stop(0.05, #48abab), color-stop(1, #6eccb3));
    background: -webkit-linear-gradient(top, #48abab 5%, #6eccb3 100%);
    background: -o-linear-gradient(top, #48abab 5%, #6eccb3 100%);
    background: -webkit-gradient(linear, left top, left bottom, color-stop(5%, #48abab), to(#6eccb3));
    background: linear-gradient(to bottom, #48abab 5%, #6eccb3 100%);
    filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#48abab', endColorstr='#6eccb3',GradientType=0);
    background-color: #48abab;
    color: #16161d; }
  button:active {
    position: relative;
    top: 1px; }



